.about {
    background-color: white;
    padding: 2rem 1rem;
    border-radius: var(--border-radius);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .about h2 {
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .about p {
    line-height: 1.6;
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
  }