.projects {
    padding: 2rem 1rem;
    
  }
  
  .projects h2 {
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .project-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    justify-content: center;
  }
  
  .project {
    flex: 1 1 200px;
    min-width: 200px;
    background-color: white;
    padding: 1rem;
    border-radius: var(--border-radius);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 40%;
    line-height: 1.2;
  }

  .project ul {
    list-style-type: none;
  }
  
  .project img {
    width: 100%;
    border-radius: var(--border-radius);
  }
  
  .project h3 {
    font-size: 1.2rem;
    color: var(--secondary-color);
    margin: 0.5rem 0;
  }
  
  .project p {
    font-size: 0.9rem;
    line-height: 1.2;
  }

  @media (max-width: 600px) {
    .project {
      flex: 1 1 100%;
      max-width: 100%;
    }
  }